@font-face {
  font-family: "Cabinet-Grotesk";
  src: url("../../vendor/fonts/CabinetGrotesk-Bold.woff") format("woff"),
    url("../../vendor/fonts/CabinetGrotesk-Bold.woff2") format("woff2");
}

.song-card {
  position: relative;
  text-align: center;
}

.song-card__image {
  width: 300px;
  height: 300px;
  border-radius: 19px;
  border: 1px solid rgba(0, 0, 0, 0.3);
  background: #e9e9e9;
  cursor: pointer;
}

.song-card__name-container {
  position: absolute;
  margin-top: 16px;
  top: 16px;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 73px;
  min-height: 24px;
  display: flex;
  justify-content: center;

  cursor: pointer;
}

.song-card__name {
  margin: auto;
  padding: 2px 12px;
  width: fit-content;
  color: rgba(0, 0, 0, 0.6);
  font-family: "Cabinet-Grotesk", Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.2;
  background: #fff;
  border-radius: 4px;
}
